*{
    margin: 0;
    padding: 0;
    text-decoration: none;
}






#collection_update_banner_preview{
    width: calc(100% - 20px);
    margin: 10px;
    border-radius: 10px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0,0.3);
    transition: 200ms;
}
#collection_update_banner_preview:active{
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0,0.4);
}



.background_opened{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0,0.4);
    z-index: 5;
    pointer-events: auto;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}
.appbar .navbar_opened{
    width: 260px;
    height: 100%;
    background-color: white;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    box-shadow: 4px 0 20px 0 rgba(0, 0, 0,0.2);
    transform: translate3d(0,0,0);
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}


.background_closed{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0,0);
    z-index: 5;
    pointer-events: none;
    
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}
.appbar .navbar_closed{
    width: 260px;
    height: 100%;
    background-color: white;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    box-shadow: 4px 0 20px 0 rgba(0, 0, 0,0.2);
    transform: translate3d(-100%,0,0);
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}

.appbar{
    width: 100%;
    height: 56px;
    background-color: rgb(98,0,238);
    box-shadow: 0 2px 3px 0px rgba(38, 37, 39, 0.5);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 4;
}

.appbar .action-container{
    background-color: rgba(241, 148, 255, 0.1);
    border-radius: 100px;
    width: 42px;
    height: 42px;

    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.appbar ion-icon{
    width: 24px;
    height: 24px;
    color: white;
    font-weight: 700;
    margin: 10px;
}

.action-group{
    padding: 10px;
    font-size: 20px;
    border-bottom: 1px solid rgb(227, 227, 227);
    padding-left: 20px;
    color: gray;
}
.action-name{
    padding: 10px;
    font-size: medium;
    padding-left: 40px;
    border-bottom: 1px solid rgb(227, 227, 227);
}
.action-name:active{
    padding: 10px;
    font-size: medium;
    padding-left: 40px;
    border-bottom: 1px solid rgb(227, 227, 227);
    background-color: rgb(232, 232, 232);
}



/* Prduct card  desgin */
.product-card{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 6px;
    box-shadow: 0 1px 4px 0 rgb(227, 227, 227);
    border-top: 1px solid rgb(245, 245, 245);
    overflow: hidden;
    transition-duration: 200ms;
}
.product-card:hover{
    box-shadow: 0 2px 4px 1px rgb(227, 227, 227);
    background-color: rgb(245, 245, 245);
}
.product-card:active{
    box-shadow: 0 2px 6px 2px rgb(227, 227, 227);
    background-color: rgb(240, 240, 240);
}


.product-cardx{
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}



.product-card div img {
    width: 50px;
    height: 50px;
    padding: 4px;
    border-radius: 8px;
    border: 1px solid rgb(237, 237, 237);
    margin: 4px;
    object-fit: fill;
}
.product-card .info {
    display: flex;
    flex-direction: column;
    flex: 1;
    /* padding: 5px; */
    padding-left: 10px;
    
}
.product-card .info .title{
    margin-top: 4px;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    color: rgb(97, 97, 97);
    font-family: 'Roboto','Noto Serif Bengali', sans-serif;

}
.product-card .info .description{
    margin-top: 4px;
    font-size: 15px;
    color: gray;
    font-family: 'Roboto', sans-serif;
    color: rgb(161, 161, 161);
    font-family: 'Roboto','Noto Serif Bengali', sans-serif;
}
.product-card .price{
    padding: 20px;
    padding-right: 0;
    padding-top: 5px;
    font-size: 20px;
    color: rgb(85, 85, 85);
    font-family: 'Roboto', sans-serif;
}
.product-card ion-icon{
    width: 24px;
    height: 24px;
    color: white;
    font-weight: 700;
    padding: 10px;
    color: gray;
}

