.edit-collection-select-open #select-collection-bg{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0,0.3);
    z-index: 8;
    top: 0;
    left: 0;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}


.edit-collection-select-open #select-collection-container{
    width: 90%;
    height: 80%;
    left: 0;
    background-color: white;
    position: fixed;
    top: 0;
    z-index: 20;
    margin-left: 50%;
    margin-top: 50vh;
    transform: translate(-50%,-50%);
    border-radius: 12px;
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0,0.5);
    /* padding: 10px; */
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
    display: flex;
    flex-direction: column;
}
.edit-collection-select-open #select-collection-container h2{
    padding: 10px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0,0.2);
}
.edit-collection-select-open #select-collection-container div{
    border-radius: 8px;
}
.edit-collection-select-open #select-collection-container .colleciton-container{
    padding: 10px;
    overflow: auto;
    flex: 1;
}
#select-collection-container #action{
    display: flex;
    width: 100%;
    position: absolute;
    bottom: 0;
    box-shadow: 0 -2px 2px 0 rgba(0,0,0,0.1);
    border-radius: 0 0 12px 12px;
}
#select-collection-container #action div{
    flex: 1;
}


.edit-collection-select-open {
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
    pointer-events: auto;
}


.edit-collection-select-close {
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
    pointer-events: none;
    opacity: 0;
}

@media only screen and (min-width: 600px){
    .edit-collection-select-open {
        transition-duration: 200ms;
        transition-timing-function: ease-in-out;
        pointer-events: auto;
        width: 400px;
    }
    
    
    .edit-collection-select-close {
        transition-duration: 200ms;
        transition-timing-function: ease-in-out;
        pointer-events: none;
        opacity: 0;
        width: 400px;
    }
    
    .edit-collection-select-open #select-collection-container{
        width: 400px;
        left: 0;
    }
}


